.index {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

header {
  width: 100%;
}

footer {
  width: 100%;
}

.logo {
  width: 262px;
  height: 120px;
  margin:0 auto;
}

.logo.logo-search {
  margin-top: 15px;
}

.index-copy {
