}
/* .view-btn:hover {
  background: #CCECFF;
} */
.view-btn {
  transition: .32s ease-in-out;
  background: #72CEFF;
  border-bottom: solid 2px #62B4EF;
  width: 100%;
  height: 43px;
  padding: 10px 0;
  border-left: none;
  border-top: none;
  border-right: none;
  font-size: 14px;
  font-family: 'Helvetica Neue';
  font-weight: bold;
  outline: none;
  cursor: pointer;
}

.view-btn-link {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
}

.search-bar-container {
  position: relative;
  width: 25%;
  margin: 0 auto;
}

.search-bar {
  position: absolute;
  left: 0;
  right: 0;
}

.categories {
  position: absolute;
  display: flex;
  width: 101%;
  top: 35px;
  left: -1px;
  height: 40px;
  align-items: center;
}

.categories > div {
  background: #F0F0F0;
    flex: 1 1;
    text-align: center;
    transition: .15s ease-in-out;
    height: 100%;
}

.categories > div > a {
  text-decoration: none;
  color: #333333;
  font-size: 12px;
  display: block;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.categories > div:hover {
  background: #F8F8F8;
}

.active {
  background: #5896C4!important;
  color:#fff!important;
}

.active a {
  color: #fff!important;
}

.search-bar-input {
  width: 100%;
  height: 35px;
  position: relative;
  border-left: solid 2px #5896C4;
  border-right: none;
  border-bottom: none;
  border-right: none;
  border-top: none;
  outline: none;
  text-indent: 12px;
  font-size:15px;
  font-weight: bold;
  background: #FAFAFA;
  color: #CBCBCB;
}

.search-btn {
  width: 25px;
  height: 25px;
  position: absolute;
  right: 0px;
  outline: none;
  border:none;
  text-indent: -9999px;
  background: url('./search.svg') no-repeat;
  top: 10px;
}

.search-status {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  height: 100%;
}

.collection-icon {
  border-radius: 100%;
  color: #fff;
  background: green;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}

.collection-icon {
  border-radius: 100%;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}

.collection {
  background: green;
}

.wantlist {
  background: red;
}

@media(min-width: 485px) {
  .search-bar-container{
    width: 74%;
  }
}

@media(min-width: 1075px) {
  .search-bar-container{
    width: 30%;
  }
}

@media(max-width: 485px) {
  .search-bar-container{
    width: 85%;
  }
}

.search-page {
  height: 65vh;
  align-items: center;
  display: flex;
}

.search-dropdown-items {
  position: absolute;
  top: 74px;
  width: 101%;
  box-shadow: 0 3px 6px rgba(113, 113, 113, 16%);
  transition: .32s ease-in-out;
  margin-bottom: 25px;
}

.dropdown-results-link {
  color: #333;
  text-decoration: none;
}

.result-image {
  width: 75px;
}

.result-item {
  display: flex;
  background: #FAFAFA;
  border-top: solid 1px #F2F2F2;
  transition: .32s ease-in-out;
}

.result-item:hover {
  background: #ffffff;
}

.result-title {
  font-size: 12px;
  padding: 6px 5px;
  width:100%;
  color: #3079CE;
}

.result-title:visited {
  color: #98158B
}

.result-title p {
  margin:0;
}

.result-status {
  display: flex;
  align-items: center;
  padding: 0 10px 0px 0px;
}

.collection-icon {
  width:17px;
  height:17px;
}

img {
  width: 100%;
  height: 100%;
}

.hide {
  display: none;
}

span.format {
  color: #333;
}

/* Spinner Styles */
.spinner {
  margin: 0 auto 0;
  width: 40px;
  text-align: center;
}

.spinner > div {
  width: 8px;
  height: 8px;
  background-color: #333;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% { 
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% { 
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}

footer p {
  text-align: center;
  font-size: 10px;
  text-transform: uppercase;
}

/* Release Styles */
