  .container.release, 
  .container.artist {
      padding: 0 5px;
      width: 65%;
  }
}

@media (min-width: 1075px){
  .container.release {
      padding: 0 5px;
      width: 41%;
  }
}

@media (max-width: 715px){
  .container.release,
  .container.artist,
  .container.label {
